<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper" @click="$emit('close')">
        <div class="modal-container" @click.stop>
          <button class="modal-default-button" @click="$emit('close')">
            <b>x</b>
          </button>
          <div v-if="body1" class="tickets">
            <div class="modal-header izrades">
              {{subTitle1}}
            </div>
            <div class="modal-body modalBody">
              <div v-for="(p, pIndex) in body1">{{p.p}}
              </div>
            </div>
            <div class="modal-header">
              {{subTitle2}}
            </div>
            <div class="modal-body modalBody">
              <div v-for="(p, pIndex) in body2">{{p.p}}
              </div>
            </div>
          </div>
          <div v-if="title" class="modal-header">
            <div v-for="(p, pIndex) in title">{{p.p}}
            </div>
            <div v-if="date" class="date">
              <br>{{date}}
              <br> {{spot}}
            </div>
            <div v-if="date1" class="date">
              <br>{{date1}}
              <br> {{date2}}
              <br> {{spot}}
            </div>
          </div>
          <div v-if="crew" class="row crew">
            <div v-for="(p, pIndex) in crew">{{p.p}}
            </div>
          </div>
          <div v-if="body" class="modal-body modalBody">
            <div v-for="(p, pIndex) in body">{{p.p}}
            </div>
          </div>
          <div v-if="venue" class="modal-body modalBody">
            <div v-for="(p, pIndex) in venue" class="modal-body modalBody"><a :href="p.link" target="maps">{{p.p}}</a>
            </div>
            <iframe
              name="maps"
              class="maps"
              width="600"
              height="350"
              frameborder="0" style="border:0"
              allowfullscreen>
            </iframe>
          </div>
          <Gallery v-if="showImages" :images="images"/>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import Gallery from "./Gallery";

  export default {
    name: "Modal",
    components: {Gallery},
    props: ['body', 'body1', 'title', 'date', 'venue', 'spot', 'crew', 'images', 'showImages', 'subTitle1', 'subTitle2', 'body2', 'date1', 'date2']
  }
</script>

<style scoped>

  .home {
    padding-top: 35px;
  }

  .modal-header {
    color: #369AA2;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    text-transform: uppercase;
    padding-bottom: 2%;
  }

  .izrades {
    color: #EF3D83;
  }

  .maps {
    float: right;
  }
  @media only screen and (max-width: 600px) {
    .maps {
      width: 200px !important;
      float: inside;
    }
  }

  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  .crew {
    color: #777777;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 11px;
    line-height: 16px;
    margin: 0 0 24px;
    text-align: justify;
    text-justify: inter-word;
    display: block;
  }

  .tickets {
    text-align: center;
  }

  .date {
    text-align: left;
    font-size: 12px;
  }
  .modalBody {
    color: #777777;
    font-size: 14px;
  }
  .modalBody > a {
    color: #777777;
    font-size: 14px;
  }

</style>



